.contact {
  .info-section {
    width: 35%;
  }

  .content-area {
    margin-left: 35%;
    background: $color3;
    width: 65%;
    height: 100%;

    .img-container {
      width: 30%;
      display: inline-block;
      position: relative;
      height: 100%;

      .form-section {
        position: absolute;
        top: 56px;
        left: -250px;
        width: 500px;
        height: calc(100% - 112px);
        background: $color2;
        color: $color3;
        padding: 46px;
        -webkit-box-shadow: 0px 0px 74px 17px rgba(0, 0, 0, 0.34);
        -moz-box-shadow: 0px 0px 74px 17px rgba(0, 0, 0, 0.34);
        box-shadow: 0px 0px 74px 17px rgba(0, 0, 0, 0.34);
        overflow-y: scroll;
        overflow-x: hidden;

        h4 {
          margin-top: 2rem;
          font-size: 1.3rem;
          margin-bottom: 4rem;
          font-weight: 500;
        }

        input[type="text"],
        input[type="email"],
        textarea {
          width: 100%;
          background: transparent;
          border: none;
          border-bottom: 2px solid $color5;
          margin-bottom: 2rem;
          font-size: 0.9rem;
          text-transform: uppercase;
          line-height: 1.2;
          color: $color5;
          padding: 15px 0;
        }

        button[type="submit"] {
          background: $color1;
          border: none;
          font-size: 1.1rem;
          color: white;
          width: 100%;
          text-decoration: none;
          text-transform: uppercase;
          padding: 17px 30px;
          border-radius: 30px;
          text-align: center;
          -webkit-border-radius: 30px;
          -moz-border-radius: 30px;
        }
      }
    }
    .contact-section {
      vertical-align: top;
      display: inline-block;
      width: 70%;
      float: right;
      padding: 56px 0 0;

      .contact-info {
        .contact-box {
          max-width: 550px;

          padding: 30px;
          transition: all 0.3s ease-in-out;
          cursor: pointer;
          &:hover {
            -webkit-box-shadow: 0px 20px 74px 17px rgba(0, 0, 0, 0.12);
            -moz-box-shadow: 0px 20px 74px 17px rgba(0, 0, 0, 0.12);
            box-shadow: 0px 20px 74px 17px rgba(0, 0, 0, 0.12);
          }
          h5 {
            font-weight: 600;
            font-size: 1.1rem;
            margin: 0 0 1.5rem;
          }
          p {
            color: $color5;
            font-weight: 300;
            line-height: 1.4;
            margin: 0 0 2rem;
          }
        }
        h1 {
          font-size: 2.3rem;
          font-weight: 500;
          line-height: 1.2;
          margin: 0 0 1rem;
          span {
            color: $color1;
          }
        }
      }
    }
  }
}

// Phone
@media (min-width: 0px) and (max-width: 767px) {
  .contact {
    .menu-icon {
      top: -3px;
      color: white;
      right: 20px;
      .fa {
        color: white;
      }
    }
    .info-section {
      // display: none;
      position: static;
      padding: 10px 20px;
      .logo {
        height: 50px;
        width: 70px;
        font-size: 0.7rem;
      }
      .info-box {
        display: none;
      }
    }
    .content-area {
      margin-left: 0;
      width: 100%;
      position: relative;
      overflow: auto;
      &:after {
        content: "";
        display: table;
        clear: both;
      }
      .contact-section {
        // position: absolute;
        // display: block;
        width: 100%;
        float: left;
        padding: 20% 20px 0;
        // top: 0;
        max-width: 400px;

        .ghost-btn,
        .primary-btn,
        .contact-btn {
          margin-bottom: 20px;
        }
      }

      .img-container {
        padding: 56px 20px 0;
        width: 100%;
        height: auto;

        .form-section {
          position: static;
          width: 100%;
          height: auto;
          overflow: auto;
        }
      }
    }
  }
}

// Tablet
@media (min-width: 768px) and (max-width: 991px) {
  .contact {
    .content-area {
      height: auto;
      .contact-section {
        width: 70%;
        float: right;
        padding: 20% 20px 0;
        .ghost-btn,
        .primary-btn,
        .contact-btn {
          margin-bottom: 20px;
        }
      }
      .img-container {
        padding: 56px 20px 0;
        height: auto;

        .form-section {
          position: static;
          width: 100%;
          height: auto;
          overflow: auto;
        }
      }
      &:after {
        content: "";
        display: table;
        clear: both;
      }
    }
  }
}

// Laptop
@media (min-width: 992px) and (max-width: 1199px) {
  .contact {
    .info-section {
      .icon-section {
        a {
          font-size: 1.4rem;
        }
      }
    }

    .content-area {
      height: auto;
      &:after {
        content: "";
        display: table;
        clear: both;
      }
      .contact-section {
        width: 70%;
        padding: 20% 60px 0;
        max-width: 800px;
        float: left;
        .ghost-btn,
        .primary-btn,
        .contact-btn {
          margin-bottom: 20px;
        }
      }
      .img-container {
        padding: 56px 20px 0;
        float: left;
        height: auto;
        width: 100%;

        .form-section {
          position: static;
          width: 100%;
          float: left;
          height: auto;
          overflow: auto;
        }
      }
    }
  }
}

// extra big
