        .primary-btn,
        .contact-btn {
          margin-bottom: 20px;
        }
      }
      .img-container {
        padding: 56px 20px 0;
        float: left;

        .form-section {
          // position: static;
          // width: 100%;
          left: 157px;
          width: 400px;
        }
      }
    }
  }
}

